<template>
  <div>
    <notifications></notifications>
    <router-view :key="$route.fullPath"></router-view>
    <video autoplay muted loop id="myVideo">
      <source src="https://assets.blz-contentstack.com/v3/assets/blt3452e3b114fab0cd/blte6c99959afd77fb0/6029ceb4c484333be943dc33/bcc_masthead_1.webm" type="video/webm">
    </video>
  </div>
</template>

<script>
  export default {
    methods: {
      disableRTL() {
        if (!this.$rtl.isRTL) {
          this.$rtl.disableRTL();
        }
      },
      toggleNavOpen() {
        let root = document.getElementsByTagName('html')[0];
        root.classList.toggle('nav-open');
      }
    },
    mounted() {
      this.$watch('$route', this.disableRTL, { immediate: true });
      this.$watch('$sidebar.showSidebar', this.toggleNavOpen)
    }
  };
</script>


